                                                                        <div className="jsx-1546557212 mediaContainer"><img alt="yutu rover chang e 4" src="https://cdn.technologyreview.com/i/images/20190419yutu2-tracks-near-lander-release-march-2019-ourspace.png?sw=768&amp;cx=0&amp;cy=0&amp;cw=1360&amp;ch=765" srcSet="https://cdn.technologyreview.com/i/images/20190419yutu2-tracks-near-lander-release-march-2019-ourspace.png?sw=768&amp;cx=0&amp;cy=0&amp;cw=1360&amp;ch=765 1x, https://cdn.technologyreview.com/i/images/20190419yutu2-tracks-near-lander-release-march-2019-ourspace.png?sw=1536&amp;cx=0&amp;cy=0&amp;cw=1360&amp;ch=765 2x" tabindex="-1" className="jsx-1546557212 " /></div>
